*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: -webkit-fill-available;
}

body {
  height: 100vh;
  height: -webkit-fill-available;
  overflow: hidden;
}


#root{
  height: 100%;
}

/* classname NavLink */
.active > *:first-child{
  display: none;
}

.active > *:last-child{
  display: block;
}

.unactive > *:first-child{
  display: block;
}

.unactive > *:last-child{
  display: none;
}

/* menu animations */
.menuAnimationTop{
  animation-name: menuAnimationTop;
  animation-duration: .25s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.menuAnimationBottom{
  animation-name: menuAnimationBottom;
  animation-duration: .25s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes menuAnimationTop {
  from{
    bottom: -100%;
  }
  to{
    bottom: 0;
  }
}

@keyframes menuAnimationBottom {
  from{
    bottom: 0;
  }
  to{
    bottom: -100%;
  }
}